import * as React from 'react';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { useNavigate } from 'react-router';
import { IconButton } from '@mui/material';
import DeleteForeverOutlinedIcon from '@mui/icons-material/DeleteForeverOutlined';
import { roundTwoDec } from '../helpers/processToResult';
import { useDispatch } from 'react-redux';
import { deleteLeadsFromDb } from '../businessCases/businessCaseSlice';

export default function BusinessCaseCard(props) {
  const navigate = useNavigate();
  const dispatch = useDispatch()

  const handleSeeDetailClick = ()=>{
    navigate('/details/'+props.data.name, {state:{data:props.data}});
  }

  const handleDelete = ()=>{
    dispatch(deleteLeadsFromDb(props.data.id));
  }

  return (
    <Card sx={{ minWidth: 275 , margin: '1rem 0'}}>
      <CardContent>
        <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
          Affaire No {props.number + 1}
        </Typography>
        <Typography variant="h5" component="div">
          {props.data.name}
        </Typography>
        <Typography sx={{ mb: 1.5 }} color="text.secondary">
          
        </Typography>
        <Typography variant="body2">
          Durée en jour : {props.data.duration}.
          <br />
          Coût total : {roundTwoDec(props.data.totalCosts)} €
          <br />
          Bénéfices potentiels : {roundTwoDec(props.data.totalProfit)} €
          <br />
          Marge : {roundTwoDec(props.data.margin)} %
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small" variant="outlined" onClick={handleSeeDetailClick}>Détails</Button>
        <IconButton color='error' onClick={handleDelete}><DeleteForeverOutlinedIcon/></IconButton>
      </CardActions>
    </Card>
  );
}
