import { Button } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useLocation, useNavigate } from 'react-router';
import DetailsTable from './DetailsTable';
import { updateLeadsInDb, selectBCListStatus } from '../businessCases/businessCaseSlice';
import CustomSnackBar from './CustomSnackBar';


const DetailView = (props)=>{
    const useDidMount = ()=>{
        const [didMount, setDidMount] = useState(false)
        useEffect(() => { setDidMount(true) }, [])
        return didMount
      }
    const didMount = useDidMount();

    const {state} = useLocation();
    const {data} = state;
    const [localData,setLocalData] = useState(data);
    const dispatch = useDispatch();
    const listStatus = useSelector(selectBCListStatus);
    const navigate = useNavigate();

    const handleChanges= (changes)=>{
        setLocalData(changes);
    }

    const handleSave=()=>{
        dispatch(updateLeadsInDb(localData));
    }

    const handleQuit = ()=>{
        navigate('/');
    }

    useEffect(()=>{
        if(didMount){
            if(listStatus ==="success"){
                navigate('/', {state:{update: true, newEntryName:localData.name}});
            }
        }
        
    }, [listStatus])

    return(
        <div>
            <DetailsTable data={localData} callBack={handleChanges} saveCallBack={handleSave} cancelCallBack={handleQuit}/>
            {listStatus === "failure" && <CustomSnackBar severity="error" message="Impossible de modifier l'affaire, réessayez plus tard" />}
        </div>
    )
}

export default DetailView;