import { createAsyncThunk, createSlice } from '@reduxjs/toolkit'
import { useLiveQuery } from 'dexie-react-hooks';
import {db} from '../app/db';

export const addLeadsToDb = createAsyncThunk("businessCase/addLead", async (data)=>{
  
  try{
    const result = await db.leads.add(data, data.id);
    if (result=== data.id){
      return data;
    }else{
      throw(Error);
    }
  }catch(error){
    return "error";
  }
    
})

export const fetchLeadsToDb = createAsyncThunk("businessCase/fetchLeads", async ()=>{
  try{
    const leadsList = await db.leads.toArray()
    return leadsList;
  }catch(error){
    return "error";
  }  
})

export const deleteLeadsFromDb = createAsyncThunk("businessCase/deleteLead", async (id)=>{
  try{
    db.leads.delete(id);
    return id;
  }catch(error){
    return "error";
  }
})

export const updateLeadsInDb = createAsyncThunk("businessCase/updateLead", async (data)=>{
    let result = await db.leads.update(data.id, data).then((updated)=>{
      if(updated){
        return data;
      }else{
        return "error"
      }
    });
   return result;
})

export const businessCaseSlice = createSlice({
  name: 'businessCase',
  initialState: {
    data:[],
    status:"idle"
  },
  // reducers: {
  //   addOne: (state,action) => {
  //     // Redux Toolkit allows us to write "mutating" logic in reducers. It
  //     // doesn't actually mutate the state because it uses the immer library,
  //     // which detects changes to a "draft state" and produces a brand new
  //     // immutable state based off those changes
  //     state.data.push(action.payload);
  //   }
  // },
  extraReducers: builder => {
    builder
      .addCase(addLeadsToDb.pending, (state) => {
        state.status = 'loading'
      })
      .addCase(addLeadsToDb.fulfilled, (state, action) => {
        if(action.payload.id){
          state.data.push(action.payload);
          state.status = 'success'
        }else{
          state.status = 'failure'
        }
        
      })
      .addCase(fetchLeadsToDb.pending, (state) => {
        state.status = 'loading'
      })
      .addCase(fetchLeadsToDb.rejected, (state) => {
        state.status = 'failure';
      })
      .addCase(fetchLeadsToDb.fulfilled, (state, action) => {
        if(action.payload !== "error"){
          state.data = action.payload.map(item =>{
            return item;
          })
          state.status = 'success'
        }else{
          state.status = 'failure'
        }
      })
      .addCase(deleteLeadsFromDb.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(deleteLeadsFromDb.rejected, (state) => {
        state.status = 'failure';
      })
      .addCase(deleteLeadsFromDb.fulfilled, (state, action) => {
          state.data = state.data.filter(item =>{
            return item.id !== action.payload;
          })
          state.status = 'success'
      })
      .addCase(updateLeadsInDb.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(updateLeadsInDb.rejected, (state) => {
        state.status = 'failure';
      })
      .addCase(updateLeadsInDb.fulfilled, (state, action) => {
          // let index = state.data.findIndex(item=>{
          //   return item.id === action.payload.id
          // })
          // state.data[index] = action.payload;
          state.status = 'success'
      })
  }
})

export const { addOne, updateOne, deleteOne } = businessCaseSlice.actions
export const selectBCList = state => state.businessCase.data;
export const selectBCListStatus = state => state.businessCase.status;

export default businessCaseSlice.reducer

