import { Button, FormControl, FormGroup, TextField, ToggleButton, ToggleButtonGroup, Typography, ClickAwayListener, Zoom } from '@mui/material';
import React, { useEffect, useState } from 'react';
import BusinessCaseCard from "./BusinessCaseCard";
import { useSelector, useDispatch } from 'react-redux';
import {fetchLeadsToDb,selectBCList, selectBCListStatus} from '../businessCases/businessCaseSlice';
import {useNavigate} from 'react-router-dom';
import { useLocation } from 'react-router';
import CustomSnackBar from './CustomSnackBar';
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';


const ListView = ()=>{
    const dispatch = useDispatch()
    const [newBusinessName, setNewBusinessName] = useState('');
    const [scenarioBase, setScenarioBase] = useState('');
    const navigate = useNavigate();
    const businessList = useSelector(selectBCList);
    const {state} = useLocation();
    const listLength = businessList.length;
    const [createNew, setCreateNew] = useState(false);

    useEffect(()=>{
        dispatch(fetchLeadsToDb());
    },[listLength])

    const handleChange = (e)=>{
        setNewBusinessName(e.target.value);
    }

    const handleScenarioChange = (e)=>{
        setScenarioBase(e.target.value);
    }

    const handleClick = ()=>{
        navigate('/new', {state:{name:newBusinessName, scenarioBase: scenarioBase}});
    }

    // const cards = (props, ref)=>{
    //     return <BusinessCaseCard ref={ref} {...props}/>
    // }

    const BusinessCardsList = ()=>{
        return (businessList.map((item, index)=>{
            let key = item.id;
            return(
                <Zoom key={index} in={true}>
                    <div key={index}>
                        <BusinessCaseCard key={key} number={index} data={item}/>
                    </div>
                </Zoom>
                
            )
        })
        )
    }

    const handleNew = ()=>{
        setCreateNew(!createNew);
    }
    return(
        <div>
            <Typography variant="h3" sx={{margin:'1rem'}}>Liste de simulations</Typography>
            <FormControl fullWidth sx={{ margin:'2rem 0'}}>
            <Button disabled={createNew} variant='contained' color='primary' sx={{margin:'1rem'}} onClick={handleNew}><AddCircleOutlineIcon/>Nouveau</Button>
            
            {createNew && 
                
                <ClickAwayListener onClickAway={handleNew}>
                    <Zoom in={createNew}>
                        <FormGroup sx={{width:'100%'}}>
                            <TextField 
                                variant='standard' 
                                type='text' 
                                name='newBusiness' 
                                value={newBusinessName} 
                                id='new-business-case' 
                                label='créer une nouvelle simulation' 
                                sx={{margin:'1rem'}}
                                onChange={handleChange}
                            />
                            <ToggleButtonGroup sx={{margin:'auto', visibility: newBusinessName === '' && 'hidden'}} color='primary' value={scenarioBase} exclusive onChange={handleScenarioChange}>
                                <ToggleButton value='SalaryBased'>Salaire</ToggleButton>
                                <ToggleButton value='ServiceBased'>Coût prestataire</ToggleButton>
                                <ToggleButton value='BudgetBased'>Prix de vente</ToggleButton>
                            </ToggleButtonGroup>
                            <Button disabled={newBusinessName === '' || scenarioBase === ''} variant='contained' color='primary' sx={{margin:'1rem'}} onClick={handleClick}>Créer</Button>
                        </FormGroup>
                    </Zoom>
                </ClickAwayListener> 
            }       
            
            </FormControl>
            {businessList ? <BusinessCardsList /> : <Typography variant='subtitle1'>Aucune simulation enregistrée...</Typography>}
            {state && <CustomSnackBar severity='success' message={state.update ? 'L\'affaire ' + state.name +' a été mise à jour' : 'Nouvelle affaire ajoutée'} />}
        </div>
    )
}

export default ListView;